@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box;font-family:"Roboto",sans-serif}button{cursor:pointer}#root{width:100vw;height:100vh;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;padding:0 20px}.App{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin:0 auto;height:80%;max-height:600px;max-width:450px;width:100%;border-radius:15px;border:1px solid #000}.App .todo-header{background-color:#d3d3d3;border-radius:15px 15px 0 0;padding:8px 10px 8px 10px;border-bottom:1px solid #000}.App .todo-header .todo-header-top{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.App .todo-header .todo-header-top .date{vertical-align:middle;line-height:31px;pointer-events:none}.App .todo-header .todo-header-top .clear-button{width:25px;height:25px;background-color:#000;color:#fff;border:0;border-radius:100%}.App .todo-header .todo-header-top .clear-button:hover{-webkit-transform:rotate(360deg);transform:rotate(360deg);transition:500ms -webkit-transform;transition:500ms transform;transition:500ms transform, 500ms -webkit-transform}.App .todo-header .todo-header-top .clear-button:not(:hover){-webkit-transform:rotate(-360deg);transform:rotate(-360deg);transition:500ms -webkit-transform;transition:500ms transform;transition:500ms transform, 500ms -webkit-transform}.App .todo-header .input{word-break:break-all;width:100%;display:-webkit-flex;display:flex;margin-top:8px;white-space:pre-line;border:0;background-color:inherit;font-family:inherit;-webkit-flex-grow:1;flex-grow:1;font-size:1.4rem}.App .todo-header .input::-webkit-input-placeholder{opacity:1}.App .todo-header .input:-ms-input-placeholder{opacity:1}.App .todo-header .input::placeholder{opacity:1}.App .todo-header .input:focus{outline:0}.App .todo-body{-webkit-flex-grow:1;flex-grow:1;overflow-y:overlay;-ms-overflow-style:none;scrollbar-width:none;direction:rtl}.App .todo-body::-webkit-scrollbar{width:10px}.App .todo-body::-webkit-scrollbar-track{background:transparent}.App .todo-body::-webkit-scrollbar-thumb{background:#888}.App .todo-body .todo-list{direction:ltr;list-style-type:none}.App .todo-body .todo-list .todo-item{background-color:#f1f1f1;display:-webkit-flex;display:flex;width:100%;transition:3000ms all}.App .todo-body .todo-list .todo-item:nth-child(odd){background-color:#f7f7f7}.App .todo-body .todo-list .todo-item .text{width:100%;display:block;margin:12px 20px;word-break:break-all;-webkit-user-select:none;-ms-user-select:none;user-select:none}.App .todo-body .todo-list .todo-item .remove-btn{margin-left:auto;-webkit-flex-shrink:0;flex-shrink:0;width:30px;border:0;background-color:red;color:#fff;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.App .todo-body .todo-list .todo-item-completed{text-decoration:line-through;color:gray}.App .todo-body-mobile{-webkit-flex-grow:1;flex-grow:1;overflow-y:overlay;-ms-overflow-style:none;scrollbar-width:none;direction:rtl}.App .todo-body-mobile::-webkit-scrollbar{width:10px}.App .todo-body-mobile::-webkit-scrollbar-track{background:transparent}.App .todo-body-mobile::-webkit-scrollbar-thumb{background:#888}.App .todo-body-mobile .todo-list{direction:ltr;list-style-type:none}.App .todo-body-mobile .todo-list .todo-item{background-color:#f1f1f1;display:-webkit-flex;display:flex;width:100%}.App .todo-body-mobile .todo-list .todo-item:nth-child(odd){background-color:#f7f7f7}.App .todo-body-mobile .todo-list .todo-item .text{width:100%;display:block;margin:12px 20px;word-break:break-all;-webkit-user-select:none;-ms-user-select:none;user-select:none}.App .todo-body-mobile .todo-list .todo-item .remove-btn{margin-left:auto;-webkit-flex-shrink:0;flex-shrink:0;width:30px;border:0;background-color:red;color:#fff;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.App .todo-body-mobile .todo-list .todo-item-completed{text-decoration:line-through;color:gray}.App .swipe-left{color:#fff;background-color:red;height:100%;width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-align-items:center;align-items:center;font-weight:600;padding-right:20px}.App .todo-footer{min-height:50px;background-color:#d3d3d3;border-radius:0 0 15px 15px;border-top:1px solid #000;font-size:.9rem;padding:0 20px;display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:center;align-items:center}.App .todo-footer .item{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}.App .todo-footer .item label{margin-left:5px;-webkit-user-select:none;-ms-user-select:none;user-select:none}.App .todo-footer .item input[type=checkbox]{height:1.4em;width:1.4em;margin:0 .2em .1em 0;border:1px solid rgba(0,0,0,.3);background:-webkit-linear-gradient(#fcfcfc, #dadada);-webkit-appearance:none}.App .todo-footer .item input[type=checkbox]:checked:before{font-weight:bold;color:rgba(0,0,0,.7);content:"✓";margin-left:2px;font-size:.9em}@media screen and (max-width: 450px){#root{display:block;padding:0}.App{border:0;height:100vh;max-height:unset}.App .todo-header{border-radius:0}.App .todo-footer{border-radius:0}.input{font-size:18px !important}}.my-node-exit{opacity:1}.my-node-exit-active{opacity:0;max-height:0;transition:max-height 1000ms,opacity 1500ms}.my-node-enter{opacity:0;max-height:0}.my-node-enter-active{opacity:1;max-height:1000px;transition:max-height 4000ms,opacity 1500ms}
